import { React, useState } from "react";
import bg from "../../assets/blueBg.jpg";
import NButton from "../nbutton/NButton";
import { SafetyCertificateOutlined, MailOutlined, LockOutlined, ArrowLeftOutlined } from '@ant-design/icons';
import { jumpToLoginPage, backServer } from "../../linkUtil";
import { Input, message } from 'antd';
import './ResetPassMain.css'

export default function ResetPassMain(props) {

    const [email, setEmail] = useState('');
    const [code, setCode] = useState('');
    const [newPassword, setNewPassword] = useState('');

    const [messageApi, contextHolder] = message.useMessage();

    let mainBgStyle = {
        'backgroundImage': `url(${bg})`,
        'backgroundSize': 'cover'
    };

    let inputStyle = {
        'width': '100%',
        'heigth': '70%',
        'fontSize': '1.55rem'
    }

    let verifyInputStyle = {
        'width': '67%',
        'heigth': '70%',
        'fontSize': '1.55rem',
        'marginRight': '3%'
    }

    let sendCodeFunc = function() {
        let formData = new FormData();
        formData.append('email', email); 

        const requestOptions = {
            method: 'POST',
            body: formData,
        };
        fetch(backServer + '/api/user/sendValidateCode', requestOptions)
            .then(response => response.json())
            .then(data => {
                console.log(data);
                if (data['code'] == 200) {
                    messageApi.open({
                        type: 'success',
                        content: 'send validation code success',
                      });
                } else {
                    messageApi.open({
                        type: 'error',
                        content: 'send validation code error ',
                      });
                }
            });
    }

    let resetPassFunc = function() {
        let formData = new FormData();
        formData.append('email', email); 
        formData.append('validate_code', code); 
        formData.append('new_password', newPassword); 

        const requestOptions = {
            method: 'POST',
            body: formData,
        };
        fetch(backServer + '/api/user/resetPassword', requestOptions)
            .then(response => response.json())
            .then(data => {
                console.log(data);
                if (data['code'] == 200) {
                    messageApi.open({
                        type: 'success',
                        content: 'reset password success',
                      });
                      setInterval(
                        () => { jumpToLoginPage() }, 1200
                      )
                } else {
                    messageApi.open({
                        type: 'error',
                        content: data['msg'],
                      });
                }
            });
    }

    return (
        <div className="resetPassMain" style={mainBgStyle}>
            {contextHolder}
            {/* <div className="resetPassMain--backArrow">
                <ArrowLeftOutlined />
            </div> */}

            <div className="resetPassMain--signUpForm">
                <div className="resetPassMain--signUpForm_signUpHint">
                    <span className="resetPassMain--signUpForm_signUpHint_title">Reset Password</span>
                    <p className="resetPassMain--signUpForm_signUpHint_detail">Set your new password here and submit with the verification code sent to your email.</p>
                </div>
                <div className="resetPassMain--signUpForm_email">
                    <Input value={email} onChange={e => setEmail(e.target.value)} style={inputStyle} placeholder="email" prefix={<MailOutlined className="resetPassMain--signUpForm_inputPrefix" />} />
                </div>
                <div className="resetPassMain--signUpForm_password">
                    <Input.Password value={newPassword} onChange={e => setNewPassword(e.target.value)}  style={inputStyle} placeholder="new password" prefix={<LockOutlined className="resetPassMain--signUpForm_inputPrefix" />} />
                </div>
                <div className="resetPassMain--signUpForm_verifyCode">
                    <Input value={code} onChange={e => setCode(e.target.value)} style={verifyInputStyle} placeholder="verify code" prefix={<SafetyCertificateOutlined className="resetPassMain--signUpForm_inputPrefix" />} />
                    <NButton onClick={() => sendCodeFunc()} prefixIcon="SendOutlined" buttonText="Send" addStyles={{ 'height': '100%', 'width': '30%', 'borderRadius': '2.2rem', 'fontSize': '1.3rem', 'fontWeight': '400' }}></NButton>
                </div>
                <div className="resetPassMain--signUpForm_submit">
                    <NButton onClick={() => resetPassFunc()} buttonText="Submit" addStyles={{ 'height': '4.5vh', 'borderRadius': '2.2rem', 'fontSize': '1.5rem', 'fontWeight': '500' }}></NButton>
                </div>
                <div className="resetPassMain--signUpForm_login">
                    <span onClick={() => jumpToLoginPage()}><u>Log In</u></span>
                </div>
            </div>
        </div>
    )
}