import { React, useState } from "react";
import bg from "../../assets/blueBg.jpg";
import NButton from "../nbutton/NButton";
import { MailOutlined, LockOutlined } from '@ant-design/icons';
import { jumpToSignUpPage, jumpToForgetPassPage, jumpToModelPage, backServer } from "../../linkUtil";
import { Input, message } from 'antd';
import './LoginMain.css'

export default function LoginMain(props) {

    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');

    const [messageApi, contextHolder] = message.useMessage();

    let mainBgStyle = {
        'backgroundImage': `url(${bg})`,
        'backgroundSize': 'cover'
    };

    let inputStyle = {
        'width': '100%',
        'heigth': '70%',
        'fontSize': '1.55rem'
    }

    let loginSubmit = function() {
        let formData = new FormData();
        formData.append('email', email); 
        formData.append('password', password); 

        const requestOptions = {
            method: 'POST',
            body: formData,
        };
        fetch(backServer + '/api/user/login', requestOptions)
            .then(response => response.json())
            .then(data => {
                console.log(data);
                if (data['code'] == 200) {
                    messageApi.open({
                        type: 'success',
                        content: 'login success',
                      });
                    // token
                    localStorage.setItem('token', data['data']['token'])
                    setInterval(
                        () => { jumpToModelPage() },
                        1200
                    )
                } else {
                    messageApi.open({
                        type: 'error',
                        content: data['msg'],
                      });
                }
            });
    }

    return (
        <div className="loginMain" style={mainBgStyle}>
            {/* <div className="loginMain--backArrow">
                <ArrowLeftOutlined />
            </div> */}
            {contextHolder}

            <div className="loginMain--loginForm">
                <div className="loginMain--loginForm_signUpHint">
                    <span className="loginMain--loginForm_signUpHint_title">Log In</span>
                    <p className="loginMain--loginForm_signUpHint_detail">Welcome back to NeuDim!</p>
                </div>
                <div className="loginMain--loginForm_email">
                    <Input value={email} onChange={e => setEmail(e.target.value)} style={inputStyle} placeholder="email" prefix={<MailOutlined className="loginMain--loginForm_inputPrefix" />} />
                </div>
                <div className="loginMain--loginForm_password">
                    <Input.Password value={password} onChange={e => setPassword(e.target.value)} style={inputStyle} placeholder="password" prefix={<LockOutlined className="loginMain--loginForm_inputPrefix" />} />
                </div>
                <div className="loginMain--loginForm_submit">
                    <NButton onClick={() => loginSubmit()} buttonText="Log In" addStyles={{ 'height': '5.5vh', 'borderRadius': '2.2rem', 'fontSize': '1.5rem', 'fontWeight': '500' }}></NButton>
                </div>
                <div className="loginMain--loginForm_signUp_forgetPass">
                    <span className="loginMain--loginForm_signUp_link" onClick={() => jumpToSignUpPage()}><u>Sign Up</u></span>
                    <span className="loginMain--loginForm_forgetPass_link" onClick={() => jumpToForgetPassPage()}><u>Forget Password?</u></span>
                </div>
            </div>
        </div>
    )
}