import React, { useState, useEffect } from "react";
import { AlertOutlined, AppstoreOutlined, MailOutlined, CopyOutlined, ClockCircleOutlined, CheckOutlined, CloseOutlined, InboxOutlined } from '@ant-design/icons';
import { backServer } from "../../linkUtil";
import { Menu, Pagination, Modal, message, Upload, Input } from 'antd';
import Task from "../../tasks/Task";
import './ModelMain.css'
import TaskGrid from "../taskGrid/TaskGrid";
import NButton from "../nbutton/NButton";


export default function ModelMain(props) {
    const [uploadTaskName, setUploadTaskName] = useState('');
    const [uploadTaskFileReferenceMap, setUploadTaskFileReferenceMap] = useState([]);
    const [currentModelStatus, setCurrentModelStatus] = useState('queue');

    const [messageApi, contextHolder] = message.useMessage();

    const { TextArea } = Input;

    // upload task modal
    const [open, setOpen] = useState(false);
    const [confirmLoading, setConfirmLoading] = useState(false);
    const [modalText, setModalText] = useState('Submit Task');
    const showModal = () => {
        setModalText('Submit Task');
        setOpen(true);
    };

    const [currentPage, setCurrentPage] = useState(1);
    const [total, setTotal] = useState(0);
    const [taskList, setTaskList] = useState([]);

    const [leftTab, setLeftTab] = useState('model');
    let modelClass = 'modelMain--vertical_slide_menu_model_menu_unselected';
    let feedbackClass = 'modelMain--vertical_slide_menu_model_menu_unselected';
    if (leftTab === 'model') {
        modelClass = 'modelMain--vertical_slide_menu_model_menu';
    } else {
        feedbackClass = 'modelMain--vertical_slide_menu_model_menu';
    }

    function getNumFromStatus(status) {
        if (status == 'queue') return 0;
        if (status == 'process') return 1;
        if (status == 'success') return 2;
        if (status == 'fail') return 3;
        return 0;
    }

    let createTask = function () {
        console.log('create Task start');
        let uploadTaskFileReferenceList = uploadTaskFileReferenceMap;
        console.log('uploadTaskFileReferenceMap: ' + uploadTaskFileReferenceMap);
        // uploadTaskFileReferenceMap.forEach(element => {
        //     uploadTaskFileReferenceList.add(element);
        // });
        let formData = new FormData();
        formData.append('name', uploadTaskName);
        formData.append('reference', uploadTaskFileReferenceList[0]);

        const requestOptions = {
            method: 'POST',
            headers: { 'Authorization': localStorage.getItem('token') },
            body: formData,
        };
        fetch(backServer + '/api/model/task/create', requestOptions)
            .then(response => response.json())
            .then(data => {
                console.log(data);
                if (data['code'] == 200) {
                    messageApi.open({
                        type: 'success',
                        content: 'task create success',
                    });
                } else {
                    messageApi.open({
                        type: 'error',
                        content: data['msg'],
                    });
                }
                setOpen(false);
                setConfirmLoading(false);
            });
    }

    const handleOk = () => {
        console.log('handle ok called');
        setModalText('Submitting Task ... ');
        setConfirmLoading(true);
        console.log('handle ok start');
        createTask();
    };
    const handleCancel = () => {
        console.log('Clicked cancel button');
        setOpen(false);
    };

    // file upload dragger
    const { Dragger } = Upload;
    const draggerProps = {
        name: 'upload',
        multiple: false,
        action: backServer + '/api/model/upload',
        headers: {
            authorization: localStorage.getItem('token'),
        },
        onChange(info) {
            console.log(info);
            const { status, originFileObj, response } = info.file;
            console.log('status: ' + status);
            if (status !== 'uploading') {
                console.log(info.file, info.fileList);
            }
            if (status === 'done' && response !== undefined && response.code === 200) {
                message.success(`${info.file.name} file uploaded successfully.`);
                let fileUid = originFileObj.uid;
                let tempMap = uploadTaskFileReferenceMap;
                tempMap[tempMap.length] = response.data.reference
                setUploadTaskFileReferenceMap(
                    tempMap
                )
                console.log('fileUid: ' + fileUid)
                console.log(uploadTaskFileReferenceMap)
            } else if (status === 'error') {
                message.error(`${info.file.name} file upload failed.`);
            }
        },
        onDrop(e) {
            console.log('Dropped files', e);
        },
    };

    let queryTask = function (pageNum, currentStatus) {
        const requestOptions = {
            method: 'GET',
            headers: { 'Authorization': localStorage.getItem('token') }
        };
        fetch(backServer + '/api/model/task/query?status=' + getNumFromStatus(currentStatus) + '&page_num=' + (pageNum - 1) + '&page_size=4', requestOptions)
            .then(response => response.json())
            .then(data => {
                console.log(data);
                if (data['code'] == 200) {
                    let listPageData = data['data'];
                    setTotal(listPageData['total']);
                    if (listPageData['list'] == null) {
                        setTaskList([]);
                    } else {
                        let newTaskList = [];
                        for (var i = 0; i < listPageData['list'].length; i++) {
                            newTaskList[newTaskList.length] = getTaskFromObj(listPageData['list'][i]);
                        }
                        console.log('listPageData');
                        console.log(newTaskList);
                        setTaskList(newTaskList);
                    }
                    messageApi.open({
                        type: 'success',
                        content: 'task query success',
                    });
                } else {
                    // messageApi.open({
                    //     type: 'error',
                    //     content: data['msg'],
                    //   });
                }
            });
    }

    let onPageChange = function (page, pageSize) {
        setCurrentPage(page);
        queryTask(page, currentModelStatus);
    }

    useEffect(() => {
        queryTask(currentPage, currentModelStatus)
    }, []);


    const iconStyle = {
        'fontSize': '1.15rem'
    }

    const statusItems = [
        {
            label: 'Queueing',
            key: 'queue',
            icon: <CopyOutlined className="modelMain--models_container_status_menu_icon" style={iconStyle} />,
            style: { 'marginLeft': '2.5%', 'marginRight': '6%' }
        },
        {
            label: 'Processing',
            key: 'process',
            icon: <ClockCircleOutlined className="modelMain--models_container_status_menu_icon" style={iconStyle} />,
            style: { 'marginRight': '6%' }
        },
        {
            label: 'Succeeded',
            key: 'success',
            icon: <CheckOutlined className="modelMain--models_container_status_menu_icon" style={iconStyle} />,
            style: { 'marginRight': '6%' }
        },
        {
            label: 'Failed',
            key: 'fail',
            icon: <CloseOutlined className="modelMain--models_container_status_menu_icon" style={iconStyle} />,
            style: { 'marginRight': '6%' }
        }
    ]

    const onMenuClick = (e) => {
        console.log('click ', e);
        setCurrentModelStatus(e.key);
        setCurrentPage(1);
        queryTask(1, e.key);
    };

    let uploadTaskButtonStyle = {
        'marginRight': '8%',
        'marginTop': 'auto',
        'marginBottom': '4px',
        'padding': '4.5px 9px 4.5px 9px',
        'minWidth': '9%',
        'height': '50%',
        'fontSize': '1rem',
        'borderRadius': '0.4rem'
    }

    let feedbackSubmitButtonStyle = {
        'marginRight': '8%',
        'marginTop': 'auto',
        'marginBottom': '4px',
        'padding': '1.5px',
        'minWidth': '9%',
        'height': '8%',
        'width': '80%',
        'fontSize': '1rem',
        'borderRadius': '1.5rem'
    }

    let taskNameInputStyle = {
        'width': '100%',
        'heigth': '70%',
        'fontSize': '1.15rem'
    }

    // feedback info
    const [email, setEmail] = useState('');
    let inputStyle = {
        'width': '100%',
        'heigth': '70%',
        'fontSize': '1.05rem'
    }
    let textAreaStyle = {
        'width': '80%',
        'heigth': '20%',
        'fontSize': '1.05rem',
        'marginBottom': '24px'
    }

    return (
        <div className="modelMain">
            {contextHolder}

            <Modal
                className="modelMain--task_upload_modal"
                title=""
                open={open}
                onOk={handleOk}
                style={{
                    'minWidth': '45%'
                }}
                confirmLoading={confirmLoading}
                onCancel={handleCancel}
            >
                <div className="modelMain--task_upload_modal">
                    <p className="modelMain--task_upload_modal_submit_hint">{modalText}</p>
                    <div className="modelMain--task_upload_modal_name_input_container">
                        {/* <span className="modelMain--task_upload_modal_name_input_hint">Task name: </span> */}
                        <Input
                            value={uploadTaskName} onChange={e => setUploadTaskName(e.target.value)}
                            style={taskNameInputStyle}
                            placeholder="Give a task name"
                            prefix={<AppstoreOutlined className="modelMain--task_upload_modal_name_input_prefix" />} />
                    </div>
                    <Dragger {...draggerProps}>
                        <p className="ant-upload-drag-icon">
                            <InboxOutlined />
                        </p>
                        <p className="ant-upload-text">Click or drag file to this area to upload</p>
                        <p className="ant-upload-hint">
                            Support for a single or bulk upload. Strictly prohibit from uploading company data or other
                            band files
                        </p>
                    </Dragger>
                </div>
            </Modal>
            <div className="modelMain--vertical_slide_menu">
                <div className={modelClass} onClick={() => setLeftTab('model')}>
                    <AppstoreOutlined className="modelMain--vertical_slide_menu_model_menu_icon" />
                    <span>My Model</span>
                </div>
                <div className={feedbackClass} onClick={() => setLeftTab('feedback')}>
                    <AlertOutlined className="modelMain--vertical_slide_menu_model_menu_icon" />
                    <span>FeedBack</span>
                </div>
            </div>
            {
                leftTab === 'model' &&
                <div className="modelMain--models_container">
                    <div className="modelMain--models_container_menu_container">
                        <Menu
                            onClick={onMenuClick}
                            className="modelMain--models_container_status_menu"
                            selectedKeys={[currentModelStatus]}
                            mode="horizontal"
                            items={statusItems} />
                        {
                            currentModelStatus === 'queue' && <NButton suffixIcon="CloudUploadOutlined" buttonText="Upload Task" addStyles={uploadTaskButtonStyle} onClick={showModal}></NButton>
                        }
                    </div>

                    <div className="modelMain--models_container_card_container">
                        <TaskGrid taskList={taskList}></TaskGrid>
                        <div className="modelMain--models_container_card_pagination_container">
                            <Pagination style={{ 'fontSize': '1.35rem' }}
                                defaultCurrent={1} current={currentPage} total={total}
                                defaultPageSize={4} pageSize={4} onChange={onPageChange}></Pagination>
                        </div>
                    </div>
                </div>
            }
            {
                leftTab === 'feedback' &&
                <div className="modelMain--feedback_container ">
                    <div className="modelMain--feedbackForm">
                        <div className="modelMain--feedbackForm_hintText">
                            <span>Feedback</span>
                        </div>
                        <div className="modelMain--feedbackForm_feedbackText">
                            <span>Leave your feedback and suggestions about NeuDim</span>
                        </div>
                        <div className="modelMain--feedbackForm_hintText">
                            <span>Contact email:</span>
                        </div>
                        <div className="modelMain--feedbackForm_email">
                            <Input value={email} onChange={e => setEmail(e.target.value)} style={inputStyle} placeholder="email" prefix={<MailOutlined className="modelMain--feedbackForm_inputPrefix" />} />
                        </div>
                        <div className="modelMain--feedbackForm_hintText">
                            <span>Your Suggestion</span>
                        </div>
                        <TextArea style={textAreaStyle} rows={6} placeholder="What's in your mind" maxLength={6} />
                        <NButton buttonText="Submit" addStyles={feedbackSubmitButtonStyle} onClick={() => {}}></NButton>
                    </div>
                </div>
            }

        </div>
    )
}

function getTaskFromObj(taskObj) {
    return new Task(
        taskObj["task_id"],
        taskObj["name"],
        "https://youni-static-resource.oss-cn-shanghai.aliyuncs.com/%E8%BF%9D%E8%A7%84%E5%A4%B4%E5%83%8F.jpg",
        taskObj["status"],
        "2023-01-01 00:00:00",
        "http://10.15.88.38:5008/api/v1/taskdownload?task_id=" + taskObj["task_id"],
        "process fail"
    );
}