import { React, useState, useRef, useEffect } from "react";
import bg from "../../assets/blueBg.jpg";
import NButton from "../nbutton/NButton";
import { SafetyCertificateOutlined, MailOutlined, LockOutlined } from '@ant-design/icons';
import { message, Input } from 'antd';
import { jumpToLoginPage, jumpToModelPage, backServer } from "../../linkUtil";
import './SignUpMain.css'

export default function SignUpMain(props) {

    const [messageApi, contextHolder] = message.useMessage();

    let mainBgStyle = {
        'backgroundImage': `url(${bg})`,
        'backgroundSize': 'cover'
    };

    let inputStyle = {
        'width': '100%',
        'heigth': '70%',
        'fontSize': '1.55rem'
    }

    let verifyInputStyle = {
        'width': '67%',
        'heigth': '70%',
        'fontSize': '1.55rem',
        'marginRight': '3%'
    }

    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [code, setCode] = useState('');
    const [time, setTime] = useState(0)
    const timer = useRef(null)
    useEffect(() => {
        timer.current && clearInterval(timer.current);
        return () => timer.current && clearInterval(timer.current);
    }, []);

    useEffect(() => {
        if (time === 60) timer.current = setInterval(() => setTime(time => --time), 1000)
        else if (time === 0) timer.current && clearInterval(timer.current)
    }, [time])

    let sendCodeFunc = function () {
        let formData = new FormData();
        formData.append('email', email);

        const requestOptions = {
            method: 'POST',
            body: formData,
        };
        fetch(backServer + '/api/user/sendValidateCode', requestOptions)
            .then(response => response.json())
            .then(data => {
                console.log(data);
                if (data['code'] == 200) {
                    messageApi.open({
                        type: 'success',
                        content: 'send validation code success',
                    });
                    setTime(60);
                } else {
                    messageApi.open({
                        type: 'error',
                        content: 'send validation code error ',
                    });
                }
            });
    }

    let registerSubmit = function () {
        let formData = new FormData();
        formData.append('email', email);
        formData.append('password', password);
        formData.append('validate_code', code);

        const requestOptions = {
            method: 'POST',
            body: formData,
        };
        fetch(backServer + '/api/user/register', requestOptions)
            .then(response => response.json())
            .then(data => {
                console.log(data);
                if (data['code'] == 200) {
                    messageApi.open({
                        type: 'success',
                        content: 'Register success',
                    });
                    // token
                    localStorage.setItem('token', data['data']['token'])
                    setInterval(
                        () => { jumpToModelPage() },
                        1200
                    )
                } else if (data['code'] == 303) {
                    messageApi.open({
                        type: 'error',
                        content: 'Verify code not correct',
                    });
                } else {
                    messageApi.open({
                        type: 'error',
                        content: data['msg'],
                    });
                }
            });
    }

    let sendCodeButtonText = "Send";
    if (time != 0) {
        sendCodeButtonText = "Resend(" + time + ")"
    }
    let prefixIcon = "SendOutlined"
    if (time != 0) {
        prefixIcon = ""
    }

    return (
        <div className="signUpMain" style={mainBgStyle}>
            {contextHolder}
            {/* <div className="signUpMain--backArrow">
                <ArrowLeftOutlined />
            </div> */}

            <div className="signUpMain--signUpForm">
                <div className="signUpMain--signUpForm_signUpHint">
                    <span className="signUpMain--signUpForm_signUpHint_title">Sign Up</span>
                    <p className="signUpMain--signUpForm_signUpHint_detail">Welcome! Once signed up, you can use the account in NeuDim Engine Web for all functionality.</p>
                    {/* <p className="signUpMain--signUpForm_signUpHint_detail">A verification code will be sent to your email after you click the send button.</p> */}
                </div>
                <div className="signUpMain--signUpForm_email">
                    <Input value={email} onChange={e => setEmail(e.target.value)} style={inputStyle} placeholder="email" prefix={<MailOutlined className="signUpMain--signUpForm_inputPrefix" />} />
                </div>
                <div className="signUpMain--signUpForm_password">
                    <Input.Password value={password} onChange={e => setPassword(e.target.value)} style={inputStyle} placeholder="password" prefix={<LockOutlined className="signUpMain--signUpForm_inputPrefix" />} />
                </div>
                <div className="signUpMain--signUpForm_verifyCode">
                    <Input value={code} onChange={e => setCode(e.target.value)} style={verifyInputStyle} placeholder="verify code" prefix={<SafetyCertificateOutlined className="signUpMain--signUpForm_inputPrefix" />} />
                    <NButton disabled={time > 0} onClick={() => sendCodeFunc()} prefixIcon={prefixIcon} buttonText={sendCodeButtonText} addStyles={{ 'height': '100%', 'margin-top': '0%', 'width': '30%', 'borderRadius': '2.2rem', 'fontSize': '1.25rem', 'fontWeight': '400' }}></NButton>
                </div>
                <div className="signUpMain--signUpForm_submit">
                    <NButton onClick={() => registerSubmit()} buttonText="Submit" addStyles={{ 'height': '5.5vh', 'borderRadius': '2.2rem', 'fontSize': '1.5rem', 'fontWeight': '500' }}></NButton>
                </div>
                <div className="signUpMain--signUpForm_login">
                    <span onClick={() => jumpToLoginPage()}><u>Log In</u></span>
                </div>
            </div>
        </div>
    )
}