export default class Task {
    constructor(
        taskId,
        name,
        coverUrl,
        status,
        submitTime,
        objModelUrl,
        failReason
    ) {
        this.taskId = taskId;
        this.name = name;
        this.coverUrl = coverUrl;
        this.status = status;
        this.submitTime = submitTime;
        this.objModelUrl = objModelUrl;
        this.failReason = failReason;
    }
}

