import React from "react";
import NButton from "../nbutton/NButton";
import { backServer } from "../../linkUtil";
import { Menu, Pagination, Modal, message, Upload, Input } from 'antd';
import { AppstoreOutlined, CopyOutlined, ClockCircleOutlined, CheckOutlined, CloseOutlined } from '@ant-design/icons';
import './TaskCard.css'
import Axios from 'axios';

export default function TaskCard(props) {

    const [messageApi, contextHolder] = message.useMessage();

    let task = props.task

    let buttonSytles = {
        'minWidth': '24%',
        'height': '45%',
        'padding': '7px',
        'borderRadius': '1.5rem'
    }

    let iconStyle = {
        'color': '#1E90FF',
        'fontWeight': '500'
    }

    function download(url, filename) {
        const link = document.createElement("a");
        link.href = url;
        link.download = filename;
        link.click();
    }

    const axios = Axios.create();

    function downloadFile(filePath, filename) {
        axios.post(filePath, '', {
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded', //form data
            },
            'responseType': 'blob'  // Blob 
        }).then(function (response) {
            const blob = new Blob([response.data]);
            const fileName = filename;
            const linkNode = document.createElement('a');
            linkNode.download = fileName; //adownload
            linkNode.style.display = 'none';
            linkNode.href = URL.createObjectURL(blob); //Blob URL
            document.body.appendChild(linkNode);
            linkNode.click();  //
            URL.revokeObjectURL(linkNode.href); // URL 
            document.body.removeChild(linkNode);
        }).catch(function (error) {
            console.log(error);
        });
    }

    function submitProcess(taskId) {
        let formData = new FormData();
        formData.append('taskID', taskId); 

        const requestOptions = {
            method: 'POST',
            headers: { 'Authorization': localStorage.getItem('token') },
            body: formData,
        };
        fetch(backServer + '/api/model/task/submitProcess', requestOptions)
            .then(response => response.json())
            .then(data => {
                console.log(data);
                if (data['code'] == 200) {
                    messageApi.open({
                        type: 'success',
                        content: 'task submit process success',
                      });
                } else {
                    messageApi.open({
                        type: 'error',
                        content: data['msg'],
                      });
                }
            });
    }

    function getQueueingInfo(task) {
        return (
            <div className="taskCard--info_container">
                <span className="taskCard--taskName">
                    <CopyOutlined style={iconStyle}></CopyOutlined>
                    <span className="taskCard--taskName_hint">Task Name:  </span>
                    {task.name}
                </span>
                <NButton onClick={ submitProcess(task.taskId) }  buttonText="Submit Process" addStyles={buttonSytles}></NButton>
            </div>
        )
    }

    function getProcessingInfo(task) {
        return (
            <div className="taskCard--info_container">
                {contextHolder}
                <span className="taskCard--taskName">
                    <ClockCircleOutlined style={iconStyle}></ClockCircleOutlined>
                    <span className="taskCard--taskName_hint">Task Name:  </span>
                    {task.name}
                </span>
                <span className="taskCard--info"><span className="taskCard--info_hint">Submit Time:  </span>{task.submitTime}</span>
            </div>
        )
    }

    function getSucceededInfo(task) {
        return (
            <div className="taskCard--info_container">
                <CheckOutlined style={iconStyle}></CheckOutlined>
                <span className="taskCard--taskName">
                    <span className="taskCard--taskName_hint">Task Name:  </span>
                    {task.name}
                </span>
                <NButton onClick={ downloadFile(task.objModelUrl, 'model.zip') } buttonText="Download .obj"  addStyles={buttonSytles}></NButton>
            </div>
        )
    }

    function getFailedInfo(task) {
        return (
            <div className="taskCard--info_container">
                <span className="taskCard--taskName">
                    <CloseOutlined style={iconStyle}></CloseOutlined>
                    <span className="taskCard--taskName_hint">Task Name:  </span>
                    {task.name}
                </span>
                <span className="taskCard--info"><span className="taskCard--info_hint">Fail Reason:  </span>{task.failReason}</span>
            </div>
        )
    }

    return (
        <div className="taskCard">
            <div className="taskCard--cover_container">
                <img className="taskCard--cover" src={task.coverUrl} alt=""></img>
            </div>
            {
                task.status === 0 && getQueueingInfo(task)
            }
            {
                task.status === 1 && getProcessingInfo(task)
            }
            {
                task.status === 2 && getSucceededInfo(task)
            }
            {
                task.status === 3 && getFailedInfo(task)
            }
        </div>
    )
}